.boxSelect {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    position: relative;
}

.viewCount {
    padding: 10px;
    -ms-flex-align: center;
        align-items: center;
}

.textComponent {
    font-size: 10px;
    font-weight: 600;
}

.numberComponent {
    font-size: 22px;
    font-weight: 600;
}

.viewCountChildren {
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.countPageSize {
    display: block;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 2.5;
    border-radius: 5px;
    border: 1px solid black;
    cursor: pointer;
    background: greenyellow;
}

.countPageSize:active , .activeSize {
    background: lightgray;
}


@media (min-width: 320px) and (max-width: 425px) {
    .boxSelect {
        -ms-flex-pack: start;
            justify-content: flex-start;
    }
}



