.list {
    display: -ms-flexbox;
    display: flex;
    width: 30%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    list-style: none;
    padding: 0;
    margin: 20px auto;
}

.listItem {
    font-size: 14px;
    font-weight: 600;
    color: darkgrey;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.listItemActive {
    font-size: 14px;
    font-weight: 600;
    color: green;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.listItem:hover {
    color: grey;
    cursor: pointer;
}

.listItem:active {
    color: gray;
    position: relative;
    top: -5px;
}




