.item {
    width: 230px;
    height: 300px;
    border-radius: 10px;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: darkgrey;
    margin: 5px;
    border: 1px solid black;
}

.imageBox {
    width: 100%;
    height: 60%;
}

.imageBox img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}

.descriptionPost {
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    padding: 0;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.descriptionPost li {
    font-weight: 600;
    font-size: 11px;
    background: darkorange;
    margin: 5px;
    white-space: nowrap;
    padding: 4px;
    border-radius: 5px;
}

.item:hover {
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-name: slidein;
            animation-name: slidein;
}

@-webkit-keyframes slidein {
    from {
        position: relative;
        top: 0;
    }
    50% {
        position: relative;
        top: -10px;
    }

    to {
        position: relative;
        top: 0;
    }
}

@keyframes slidein {
    from {
        position: relative;
        top: 0;
    }
    50% {
        position: relative;
        top: -10px;
    }

    to {
        position: relative;
        top: 0;
    }
}



