.inputSearch {
    padding: 10px;
    outline: none;
    border: 5px solid grey;
    border-radius: 10px;
    margin: 20px 0;
}

.buttonSearch {
    background: orange;
    border: none;
    padding: 11px 18px;
    border-radius: 10px;
    margin-left: 10px;
    outline: none;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
}

.buttonSearch:hover {
    background: darkorange;
}



